body, html {
  height: 100%;
}

.main {
  height: 100%;
  text-align: center;
  justify-content: center;
  color: white;
}

.my-container {  
  height: 100%;
  width:100%;
  max-width:none;
  padding:0;
}

.my-row {
  height: 100%;
  margin-left:0;
  margin-right:0
}

.my-col {
  height: 100%;
  padding-left:0;
  padding-right:0
}

.profile {
  background-color: blue;
  background-image: url('.//assets/images/img7.jpeg');
  position: fixed;
  font-size: calc(6px + 2vmin);
  padding: 1em;
}

.profile-img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
}

.main-body {
  text-align: left;
  margin-top: 40px;
  padding-left: 20px;
  padding-right: 20px; 
  color: #4d4d4d;
  position: absolute;
  right: 0;
}

.link {
  color: white;
}